function genLunarBirthDay(item: PersonInfo) {
  if (item.solarBirthday) {
    item.lunarBirthDay = new Date(item.solarBirthday).toLocaleDateString(
      "zh-u-ca-chinese",
      { year: "numeric", month: "numeric", day: "2-digit" }
    );
    const day = parseInt(item.lunarBirthDay.slice(-2));
    const map = [
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
    ];
    item.lunarBirthDay = `${item.lunarBirthDay.slice(0, -2)}${map[day - 1]}`;
  }
}

function genAge(item: Person) {
  if (item.lunarBirthDay) {
    item.age =
      parseInt(new Date().toLocaleDateString("zh-u-ca-chinese").slice(0, 4)) -
      parseInt(item.lunarBirthDay.slice(0, 4)) +
      1;
  }
}

genealogy.map((item) => {
  genLunarBirthDay(item);
  genAge(item);
  if (item.mate) {
    if (Array.isArray(item.mate)) {
      item.mate.map((mate) => {
        genLunarBirthDay(mate);
        genAge(mate);
        return mate;
      });
    } else {
      genLunarBirthDay(item.mate);
      genAge(item.mate);
    }
  }
  return item;
});

export default genealogy;
